<template>
  <view class="activity-container">
    <view class="activety-centent">
      <view>
        <view
          class="imag-tag flex items-center pl-1 pr-2 mt-2"
          :style="{ background: getBg(item.status_str) }"
        >
          {{ item.status_str }}
        </view>
        <image :src="showImage" class="v-left" mode="aspectFill" />
      </view>
      <view class="v-right" @click="toApply(item)">
        <view class="activity-des uv-line-2">{{ item.promote_name }}</view>
        <view class="row2">
          <view
            class="sport_item"
            v-for="(sport_item, sindex) in tagList.slice(0, 5)"
            :key="sindex"
          >
            {{ sport_item }}
          </view>
          <view v-if="tagList.length > 5" class="sport_item">...</view>
        </view>
        <view class="r-center">
          <view class="activity-time">
            <uv-icon name="clock" color="#A5ADBA" size="15"></uv-icon>
            <text class="text uv-line-1">{{ item.startTime }}-{{ item.endTime }}</text>
          </view>
          <view class="activity-location">
            <uv-icon name="map" color="#A5ADBA" size="16"></uv-icon>
            <text class="text uv-line-1">{{ item.address || '暂无地址' }}</text>
          </view>
        </view>
      </view>
    </view>
    <view class="activity-footer">
      <view class="foo-left">
        <text>{{ item.train_activity_apply_num }}人参加</text>
      </view>
      <view>
        <uv-button
          @click="toApply(item)"
          type="primary"
          shape="circle"
          size="small"
          class="foo-right"
          :disabled="item.status != 2 || item.wx_is_apply != 1"
          text="去报名"
        >
          去报名
        </uv-button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'TrainItem',
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      background: '#4cd964',
    }
  },
  computed: {
    showImage() {
      return (
        this.item.cover_img_arr[0]?.http_img || 'https://cdn-static.papa.com.cn/social/default.png'
      )
    },
    tagList() {
      return this.item.tag_ids_arr ? this.item.tag_ids_arr.map((e) => e.tag_id_str) : []
    },
  },
  methods: {
    getBg(tag) {
      return (
        this.$dict.getDictLabel('applyColorList', tag, { labelKey: 'color', valueKey: 'label' }) ||
        '#9f9fa0'
      )
    },
    toApply(item) {
      uni.navigateTo({
        url: `/pages-sub/trainDetail/index?id=${item.train_activity_id}`,
      })
    },
  },
  watch: {},

  // 组件周期函数--监听组件挂载完毕
  mounted() {},
  // 组件周期函数--监听组件数据更新之前
  beforeUpdate() {},
  // 组件周期函数--监听组件数据更新之后
  updated() {},
  // 组件周期函数--监听组件激活(显示)
  activated() {},
  // 组件周期函数--监听组件停用(隐藏)
  deactivated() {},
  // 组件周期函数--监听组件销毁之前
  beforeDestroy() {},
}
</script>

